import { Row, Col } from "react-bootstrap";
import { Link, Route, useRouteMatch, useParams } from "react-router-dom";

import DataMenu from "../data/DataMenu";
import Topic from "./Topic";

function Chapter(props) {
  const { chapter_name } = useParams();
  const { url } = useRouteMatch();

  return (
    <>
      <Route exact path={url} />

      <Route path={`${url}/topic/:topic_name`}>
        <Topic chapter={chapter_name} />
      </Route>
    </>
  );
}

export function Learning() {
  const { url } = useRouteMatch();

  return (
    <Row>
      <Col>
        <Route exact path={url}>
          <Row>
            <Col xs={12} sm={12} md={12} lg={3} xl={3}>
              <DataMenu />
            </Col>
            <Col>
              <h3>ОБУЧЕНИЕ</h3>
              <br />
              Это один, два или три рисунка и вопрос. Ответы можно прослушать, используя аудиоплейеры. 
              <br />
              Отдельные вопросы проиллюстрированы видеофрагментами. Панель управления видео позволяет разворачивать видео на весь экран.
              <br />
              <br />
              Если у Вас слабый интернет, не позволяющий комфортно работать с программой, можно воспользоваться обновленным (апрель 2020) <Link to="/book">КНИЖНЫМ ВАРИАНТОМ 2020</Link>. Скачайте на свой компьютер нужные вам темы (формат pdf) и работайте с ними без подключения к интернету.
            </Col>
          </Row>
        </Route>

        <Route path={`${url}/chapter/:chapter_name`}>
          <Chapter />
        </Route>
      </Col>
    </Row>
  );
}

export default Learning;
