import { Row, Col } from "react-bootstrap";
import { Route, useRouteMatch, useParams } from "react-router-dom";

import DataMenu from "../data/DataMenu";
import Topic from "./Topic";

function Chapter(props) {
  const { chapter_name } = useParams();
  const { url } = useRouteMatch();

  return (
    <div>
      <Route exact path={url} />

      <Route path={`${url}/topic/:topic_name`}>
        <Topic chapter={chapter_name} />
      </Route>
    </div>
  );
}

export function Testing() {
  const { url } = useRouteMatch();

  return (
    <Row>
      <Col>
        <Route exact path={url}>
          <Row>
            <Col xs={12} sm={12} md={12} lg={3} xl={3}>
              <DataMenu />
            </Col>
            <Col>
              <h4>ТЕСТ</h4>
              <p>
                Это рисунок, вопрос к нему и ряд ответов (от 2 до 8), среди которых ТОЛЬКО ОДИН правильный. Попытайтесь сформулировать ответ <span className="text-danger">(это ключевой момент – самому сформулировать правильный ответ!!!)</span> и только тогда среди предложенных ответов ищите правильный.
                <br />
                <br />
                Ответы можно прослушать, используя аудиоплейеры. Отдельные вопросы проиллюстрированы видеофрагментами. Панель управления видео позволяет разворачивать видео на весь экран.
                <br />
              </p>
            </Col>
          </Row>
        </Route>

        <Route path={`${url}/chapter/:chapter_name`}>
          <Chapter />
        </Route>
      </Col>
    </Row>
  );
}

export default Testing;
