
const books = [
    { title: "1. ОРИЕНТИРОВАНИЕ В ПРОСТРАНСТВЕ", link: "/pdf/1. Ориентирование в пространстве 04.2020.pdf" },
    { title: "2. УМЕЕТЕ ЛИ ВЫ ЧИТАТЬ СЛЕДЫ?", link: "/pdf/2. Умеете ли Вы читать следы 04.2020.pdf" },
    { title: "3. В РАСТИТЕЛЬНОМ МИРЕ (Растения)", link: "/pdf/3. Знаете ли Вы растения 04.2020.pdf" },
]

export function DownloadBook() {

    const items = books.map((book, id) => {
        return (
            <a
                key={id}
                href={book.link}
                download
                target="blank"
                className="list-group-item list-group-item-action ">
                {book.title}
            </a>
        );
    });

    return (
        <div className="row justify-content-md-center">
            <div className="col col-md-6">
                <div className="list-group list-group-flush">
                    {items}
                </div>
            </div>
        </div>
    )
}
export default DownloadBook;
